.nav {
    background: rgba(0, 0, 0, 0.4);
    width: max-content;
    padding: 0.7rem 1.7rem;
    display: flex;
    gap: 0.8rem;
    border-radius: 3rem;
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    z-index: 2;
    bottom: 2rem;
    backdrop-filter: blur(15px);
}
.nav a {
    padding: 1.2rem;
    background: transparent;
    display: flex;
    font-size: 1.1rem;
    border-radius: 50%;
    color: var(--color-light);
}
.nav a:hover {
    background: rgba(0, 0, 0, 0.3);
}
.nav a.active {
    background: var(--color-primary);
    color: var(--color-bg);
}




